.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: 0;
  border: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: #000000; }

main {
  min-width: 320px; }

a[href],
button,
label,
input,
select {
  cursor: pointer; }

.wrapper {
  max-width: 960px;
  padding: 10px;
  margin: 0 auto; }

.header {
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(16, 13, 50, 0.851)), to(rgba(48, 34, 93, 0.851)));
  background: linear-gradient(0deg, rgba(16, 13, 50, 0.851) 0%, rgba(48, 34, 93, 0.851) 100%); }

.navigation {
  font-size: 13px;
  font-weight: 900;
  line-height: 25px;
  text-transform: uppercase; }

.navigation__list {
  list-style: none; }

.navigation__item a {
  text-decoration: none;
  color: #ffffff; }
  .navigation__item a:not([href]) {
    color: #00e0d0; }

.banner {
  color: #ffffff;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(16, 13, 50, 0.851)), to(rgba(48, 34, 93, 0.851)));
  background: linear-gradient(0deg, rgba(16, 13, 50, 0.851) 0%, rgba(48, 34, 93, 0.851) 100%);
  text-align: center; }

.banner__title {
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 32px;
  font-weight: 900;
  line-height: 1.1;
  letter-spacing: 0.3px; }

.banner__description {
  letter-spacing: -0.1px; }

.link {
  display: block;
  padding: 7px;
  font-family: "Hammersmith One", "Times New Roman", serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #00e0d0;
  border: 1px solid #00e0d0; }

.about {
  color: #413d4b; }

.about__title {
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 22px;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: 0.3px;
  letter-spacing: 1px;
  text-transform: uppercase; }

.about__description {
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.5px; }

.video {
